.about-skills {
	width: 900px;
	display: grid;
	grid-gap: 1em;
	grid-template-columns: repeat(3, 1fr);
}

.skill-card {
	padding: 1em;
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: var(--gray-2);
}

.skill-card img {
	width: 40px;
	margin: 10px;
}

.skill-card > p {
	font-weight: bold;
	margin: 1em 0 2em 0;
	color: var(--gray-D);
}

.tools-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.tools-list li {
	background-color: var(--gray-2);
	margin: 0.5em;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.tools-list li img {
	mix-blend-mode: luminosity;
}

.tools-list li p {
	font-size: 0.7em;
	padding: 0.5em 0 0 0;
	color: var(--gray-7);
}

@media (max-width: 1100px) {
	.about-skills {
		width: 100%;
	}
	.skill-card p {
		font-size: 0.9em;
	}
}

@media (max-width: 900px) {
	.about-skills {
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: repeat(2, 1fr);
	}

	.skill-card:nth-of-type(1) {
		grid-column: 1 / span 2;
	}

	.skill-card:nth-of-type(2) {
		grid-column: 3 / span 2;
	}

	.skill-card:nth-of-type(3) {
		grid-column: 2 / span 2;
	}
}

@media (max-width: 600px) {
	.about-skills {
		grid-template-columns: 1fr;
		grid-template-rows: repeat(3, 1fr);
	}

	.skill-card:nth-of-type(1) {
		grid-column: 1;
		grid-row: 1;
	}

	.skill-card:nth-of-type(2) {
		grid-column: 1;
		grid-row: 2;
	}

	.skill-card:nth-of-type(3) {
		grid-column: 1;
		grid-row: 3;
	}
}
